import { MaskableAttribute } from '@backbase/data-ang/arrangements';
import { AccountNumberType } from '@backbase/ui-ang/account-number-pipe';

/**
 * Converts account number to a maskable attribute
 */
export const accountNumberToMaskableAttribute = (type: AccountNumberType): MaskableAttribute => {
  switch (type) {
    case 'bban':
      return MaskableAttribute.BBAN;
    case 'iban':
      return MaskableAttribute.IBAN;
    default:
      return MaskableAttribute.Number;
  }
};
